import React, { Component } from 'react';
import Header from "./Header";
import Meta from "./meta"
import styled from "styled-components";

const MyButton = styled.button({
        background :"red",
        fontSize : "100"
});

class Page extends Component{
    render(){
        return(
            <div>
                <Meta/>
               <Header/>
               <MyButton/>
                {this.props.children}
            </div>
        );
    }
};

export default Page;